import { getPackages } from '@/actions/package';
import { Navigation } from '@/components/Navigation';
import { CheckoutForm } from '@/components/CheckoutForm';
import Link from 'next/link';
import Image from 'next/image';

export const dynamic = 'force-dynamic';

export default async function Home() {
  const packages = await getPackages();

  return (
    <main className="flex-1 flex flex-col font-sans selection:bg-blue-500/30">
      <Navigation />

      {/* HERO SECTION */}
      <section className="relative pt-32 pb-20 md:pt-48 md:pb-32 overflow-hidden flex items-center justify-center min-h-[90vh]">
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-full max-w-4xl h-96 bg-blue-600/10 blur-[120px] rounded-full pointer-events-none"></div>
        <div className="absolute bottom-0 left-0 w-full h-px bg-gradient-to-r from-transparent via-blue-500/20 to-transparent"></div>

        <div className="container mx-auto px-4 relative z-10 text-center">
          <div className="inline-flex items-center gap-2 px-4 py-2 rounded-full border border-amber-500/30 bg-amber-500/10 text-amber-400 text-sm font-semibold mb-8">
            <i className="fa-solid fa-crown"></i> Premium MLBB Services
          </div>

          <h1 className="text-5xl md:text-7xl font-extrabold tracking-tight mb-6 leading-tight max-w-4xl mx-auto">
            Gift Skin MLBB <br className="hidden md:block" />
            <span className="bg-gradient-to-r from-blue-400 to-emerald-400 bg-clip-text text-transparent">Cepat &amp; Aman</span>
          </h1>

          <p className="text-lg md:text-xl text-neutral-400 mb-10 max-w-2xl mx-auto leading-relaxed">
            Harga transparan, proses jelas, siap bantu event terbaru. Tingkatkan pengalaman bermainmu dengan skin impian di <span className="text-white font-semibold">Toro Store</span>.
          </p>

          <div className="flex flex-col sm:flex-row gap-4 justify-center items-center">
            <a href="#paket" className="w-full sm:w-auto px-8 py-4 bg-blue-500 hover:bg-blue-400 text-neutral-950 font-bold rounded-full transition-all shadow-lg shadow-blue-500/20 flex items-center justify-center gap-2 group">
              Lihat Paket <i className="fa-solid fa-arrow-right group-hover:translate-x-1 transition-transform"></i>
            </a>
            <a href="#checkout" className="w-full sm:w-auto px-8 py-4 bg-neutral-900 border border-neutral-700 hover:border-neutral-500 text-white font-bold rounded-full transition-all flex items-center justify-center gap-2">
              <i className="fa-solid fa-shopping-cart text-neutral-400"></i> Order Sekarang
            </a>
          </div>

          <div className="mt-16 flex flex-wrap justify-center gap-8 text-neutral-500 text-sm font-medium">
            <div className="flex items-center gap-2"><i className="fa-solid fa-bolt text-amber-500"></i> Fast Process</div>
            <div className="flex items-center gap-2"><i className="fa-solid fa-shield-halved text-blue-500"></i> 100% Trusted</div>
            <div className="flex items-center gap-2"><i className="fa-solid fa-hand-holding-dollar text-emerald-500"></i> Harga Jelas</div>
          </div>
        </div>
      </section>

      {/* KATALOG PAKET SECTION */}
      <section id="paket" className="py-20 bg-neutral-950 border-y border-white/5 relative z-20">
        <div className="container mx-auto px-4">
          <div className="text-center mb-12">
            <div className="inline-flex items-center gap-2 px-3 py-1.5 rounded-full border border-blue-500/20 bg-blue-500/10 text-blue-400 text-xs font-bold mb-4">
              <i className="fa-solid fa-layer-group"></i> KATALOG PAKET
            </div>
            <h2 className="text-3xl md:text-4xl font-extrabold">Pilih <span className="text-blue-400">Paket</span> Kamu</h2>
            <p className="text-neutral-500 mt-3 max-w-xl mx-auto">Tersedia berbagai pilihan paket gift skin MLBB. Klik paket untuk langsung order.</p>
          </div>

          {packages.length === 0 ? (
            <div className="text-center py-16 text-neutral-500">
              <i className="fa-solid fa-box-open text-4xl mb-4 block text-neutral-700"></i>
              Belum ada paket tersedia. Cek kembali nanti!
            </div>
          ) : (
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6 max-w-7xl mx-auto">
              {packages.map((pkg) => (
                <a
                  key={pkg.id}
                  href="#checkout"
                  className="group relative bg-neutral-900/80 border border-neutral-800 hover:border-blue-500/40 rounded-2xl overflow-hidden transition-all duration-300 hover:-translate-y-1 hover:shadow-xl hover:shadow-blue-500/10 flex flex-col"
                >
                  {/* Image */}
                  <div className="relative h-48 bg-neutral-800 overflow-hidden">
                    {pkg.imageUrl ? (
                      // eslint-disable-next-line @next/next/no-img-element
                      <img
                        src={pkg.imageUrl}
                        alt={pkg.name}
                        className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-500"
                      />
                    ) : (
                      <div className="w-full h-full flex items-center justify-center">
                        <i className="fa-solid fa-image text-4xl text-neutral-700"></i>
                      </div>
                    )}
                    {/* Gradient overlay */}
                    <div className="absolute inset-0 bg-gradient-to-t from-neutral-900 via-transparent to-transparent"></div>
                    {/* Category badge */}
                    <div className="absolute top-3 left-3">
                      <span className="text-xs font-bold px-2.5 py-1 rounded-full bg-black/60 text-neutral-300 backdrop-blur-sm border border-white/10">
                        {pkg.category}
                      </span>
                    </div>
                    {/* Stock badge */}
                    <div className={`absolute top-3 right-3 text-xs font-bold px-2.5 py-1 rounded-full backdrop-blur-sm ${
                      pkg.stock === 0 ? 'bg-red-500/20 text-red-400 border border-red-500/30'
                      : pkg.stock <= 5 ? 'bg-amber-500/20 text-amber-400 border border-amber-500/30'
                      : 'bg-emerald-500/20 text-emerald-400 border border-emerald-500/30'
                    }`}>
                      {pkg.stock === 0 ? 'Habis' : pkg.stock <= 5 ? `Sisa ${pkg.stock}` : 'Tersedia'}
                    </div>
                  </div>

                  {/* Content */}
                  <div className="p-5 flex flex-col flex-1">
                    <h3 className="font-bold text-lg leading-tight mb-1 group-hover:text-blue-400 transition-colors">{pkg.name}</h3>
                    {pkg.description && (
                      <p className="text-neutral-500 text-sm line-clamp-2 mb-3">{pkg.description}</p>
                    )}
                    <div className="mt-auto flex items-center justify-between pt-3 border-t border-neutral-800">
                      <span className="text-amber-400 font-extrabold text-lg">{pkg.price}</span>
                      <span className={`text-xs font-semibold px-2.5 py-1 rounded-full ${
                        pkg.stock === 0 ? 'bg-red-500/10 text-red-400'
                        : 'bg-blue-500/10 text-blue-400 group-hover:bg-blue-500 group-hover:text-white transition-all'
                      }`}>
                        {pkg.stock === 0 ? 'PO' : 'Order â†’'}
                      </span>
                    </div>
                  </div>
                </a>
              ))}
            </div>
          )}
        </div>
      </section>

      {/* CHECKOUT SECTION */}
      <section id="checkout" className="py-24 bg-neutral-900 relative">
        <div className="absolute top-0 left-0 w-full h-px bg-gradient-to-r from-transparent via-neutral-700 to-transparent"></div>
        <div className="container mx-auto px-4 max-w-6xl">
          <div className="grid grid-cols-1 lg:grid-cols-12 gap-12 items-start">

            {/* Left side info */}
            <div className="lg:col-span-5 space-y-8 sticky top-32">
              <div>
                <h2 className="text-3xl md:text-4xl font-bold mb-4">Mulai <span className="text-blue-400">Order</span></h2>
                <p className="text-neutral-400 leading-relaxed">
                  Isi form pemesanan dengan teliti. Pastikan ID dan Server game Anda benar.
                </p>
              </div>

              <div className="space-y-6">
                {[
                  { n: '1', color: 'text-blue-400', title: 'Pilih Paket & Isi Data', desc: 'Pilih paket sesuai kebutuhan dan isi ID MLBB kamu.' },
                  { n: '2', color: 'text-amber-400', title: 'Konfirmasi WhatsApp', desc: 'Sistem otomatis membuat pesan WA untuk admin.' },
                  { n: '3', color: 'text-emerald-400', title: 'Pembayaran & Proses', desc: 'Bayar, tunggu pertemanan 7 hari, skin dikirim!' },
                ].map(s => (
                  <div key={s.n} className="flex gap-4">
                    <div className={`w-10 h-10 rounded-full bg-neutral-800 flex items-center justify-center shrink-0 border border-neutral-700 ${s.color} font-bold`}>{s.n}</div>
                    <div>
                      <h4 className="font-bold text-lg">{s.title}</h4>
                      <p className="text-neutral-500 text-sm mt-1">{s.desc}</p>
                    </div>
                  </div>
                ))}
              </div>

              <div className="p-6 bg-neutral-950 border border-neutral-800 rounded-xl">
                <h5 className="font-bold mb-2 flex items-center gap-2">
                  <i className="fa-solid fa-circle-question text-blue-400"></i> Butuh bantuan?
                </h5>
                <p className="text-sm text-neutral-400 mb-4">Bingung pilih paket? Chat admin untuk konsultasi gratis.</p>
                <a href="https://wa.me/6281234567890?text=Halo admin, saya mau tanya soal gift skin." target="_blank"
                  className="text-sm font-semibold text-white hover:text-blue-400 transition flex items-center gap-1">
                  Chat Admin <i className="fa-solid fa-arrow-right text-xs"></i>
                </a>
              </div>
            </div>

            {/* Form */}
            <div className="lg:col-span-7">
              <CheckoutForm />
            </div>
          </div>
        </div>
      </section>

      {/* WHY TORO STORE */}
      <section id="cara-order" className="py-24 bg-neutral-950">
        <div className="container mx-auto px-4 max-w-6xl">
          <div className="text-center mb-16">
            <h2 className="text-3xl md:text-4xl font-bold mb-4">Kenapa Memilih <span className="text-amber-400">Toro Store?</span></h2>
            <p className="text-neutral-400">Layanan profesional yang mengutamakan keamanan akun dan transparansi proses.</p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-24">
            {[
              { icon: 'fa-bolt', color: 'group-hover:text-blue-500', border: 'hover:border-blue-500/30', title: 'Fast Response', desc: 'Admin sigap melayani pertanyaan dan mengurus antrian orderanmu dengan cepat.' },
              { icon: 'fa-shield-halved', color: 'group-hover:text-emerald-500', border: 'hover:border-emerald-500/30', title: 'Aman 100%', desc: 'Proses legal via gift in-game resmi tanpa perlu memberikan password akun.' },
              { icon: 'fa-clipboard-list', color: 'group-hover:text-amber-500', border: 'hover:border-amber-500/30', title: 'Antrian Jelas', desc: 'Setiap slot pertemanan 7 hari kami rekap transparan. Tidak ada janji palsu.' },
              { icon: 'fa-calendar-star', color: 'group-hover:text-purple-500', border: 'hover:border-purple-500/30', title: 'Update Event', desc: 'Selalu support event kolaborasi terbaru dan varian pass terkini.' },
            ].map(f => (
              <div key={f.title} className={`bg-neutral-900 border border-neutral-800 p-6 rounded-2xl ${f.border} transition-colors group`}>
                <i className={`fa-solid ${f.icon} text-3xl text-neutral-700 ${f.color} transition-colors mb-4 block`}></i>
                <h4 className="text-lg font-bold mb-2">{f.title}</h4>
                <p className="text-sm text-neutral-500 leading-relaxed">{f.desc}</p>
              </div>
            ))}
          </div>

          {/* FAQ */}
          <div className="max-w-3xl mx-auto border border-neutral-800 rounded-2xl overflow-hidden bg-neutral-900">
            <div className="p-8 border-b border-neutral-800 text-center bg-neutral-950">
              <h3 className="text-2xl font-bold">Frequently Asked Questions</h3>
            </div>
            <div className="divide-y divide-neutral-800">
              {[
                { q: 'Berapa lama proses gift skin?', a: 'Aturan Moonton mewajibkan pertemanan 7 hari (168 jam). Skin dikirim instan pada hari ke-8.' },
                { q: 'Apakah aman tanpa login?', a: 'Sangat aman. Kami hanya butuh ID dan Server (contoh 1234567(1234)). Skin dikirim lewat fitur gift shop.' },
                { q: 'Kalau harga event naik tiba-tiba?', a: 'Kami transparan di awal. Jika Moonton merubah harga, kami konfirmasi dulu. Refund 100% jika dibatalkan.' },
              ].map(faq => (
                <div key={faq.q} className="p-6">
                  <h4 className="font-bold text-lg mb-2 text-white">{faq.q}</h4>
                  <p className="text-neutral-400 text-sm leading-relaxed">{faq.a}</p>
                </div>
              ))}
            </div>
          </div>
        </div>
      </section>

      {/* FOOTER */}
      <footer className="bg-[#03050a] border-t border-neutral-800 pt-16 pb-8 mt-auto">
        <div className="container mx-auto px-4 max-w-6xl">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-12 mb-12">
            <div className="lg:col-span-2">
              <Link href="/" className="flex items-center mb-4">
                <Image src="/logo.png" alt="Toro Store" width={130} height={65} className="object-contain drop-shadow-lg" />
              </Link>
              <p className="text-neutral-500 text-sm max-w-md leading-relaxed mb-6">
                Layanan Gift Skin &amp; Event Pass Mobile Legends (MLBB) Terpercaya di Indonesia. Berkomitmen memberikan pelayanan yang jujur, cepat, dan 100% aman.
              </p>
              <div className="flex gap-4">
                <a href="#" className="w-10 h-10 rounded-full bg-neutral-900 border border-neutral-800 flex items-center justify-center text-neutral-400 hover:text-white hover:bg-neutral-800 transition"><i className="fa-brands fa-instagram"></i></a>
                <a href="#" className="w-10 h-10 rounded-full bg-neutral-900 border border-neutral-800 flex items-center justify-center text-neutral-400 hover:text-white hover:bg-neutral-800 transition"><i className="fa-brands fa-telegram"></i></a>
                <a href="#" className="w-10 h-10 rounded-full bg-[#25d366]/10 border border-[#25d366]/20 flex items-center justify-center text-[#25d366] hover:bg-[#25d366] hover:text-white transition"><i className="fa-brands fa-whatsapp"></i></a>
              </div>
            </div>

            <div>
              <h4 className="font-bold mb-4">Navigasi</h4>
              <ul className="space-y-3 text-sm text-neutral-500">
                <li><Link href="/" className="hover:text-blue-400 transition">Beranda</Link></li>
                <li><Link href="#paket" className="hover:text-blue-400 transition">Katalog Paket</Link></li>
                <li><Link href="#checkout" className="hover:text-blue-400 transition">Pesan Sekarang</Link></li>
                <li><Link href="#cara-order" className="hover:text-blue-400 transition">FAQ &amp; Panduan</Link></li>
                <li><Link href="/admin" className="hover:text-blue-400 transition">Admin Login</Link></li>
              </ul>
            </div>

            <div>
              <h4 className="font-bold mb-4">Disclaimer</h4>
              <p className="text-xs text-neutral-600 leading-relaxed">
                Toro Store berdiri secara independen dan bukan bagian dari Moonton. Semua logo dan merek dagang Mobile Legends adalah properti Moonton. Transaksi dijamin amanah.
              </p>
            </div>
          </div>

          <div className="pt-8 border-t border-neutral-900 text-center text-sm text-neutral-600">
            <p>&copy; {new Date().getFullYear()} Toro Store. Algoritma &amp; Sistem dilindungi hak cipta.</p>
          </div>
        </div>
      </footer>

      {/* Floating WA CTA (Mobile) */}
      <a href="https://wa.me/6281234567890" target="_blank"
        className="fixed bottom-6 right-6 w-14 h-14 bg-[#25d366] text-white rounded-full flex items-center justify-center text-3xl shadow-lg shadow-[#25d366]/30 hover:scale-110 transition-transform z-50 md:hidden"
        aria-label="Chat WhatsApp">
        <i className="fa-brands fa-whatsapp"></i>
      </a>
    </main>
  );
}
